.container {
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 20px;
}

.card {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  gap: 40px;
}

.rate {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}

/* @media (max-width: 768px) {
    .card{
      justify-content: center;
    }
  } */

.incdec {
  display: flex;
  align-items: center;
  gap: 5px;
}

.emptycartimage {
  width: 30%;
}

.oops {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.summary {
  /* width: 50%; */
  background-color: #f6f6f6;
  border-radius: 0;
}

.cardSummary {
  display: flex;
  justify-content: flex-end;
}

.layout {
  display: flex;
  /* flex-wrap: wrap; */
  justify-content: space-between;
}

@media (max-width: 768px) {
  .layout {
    flex-direction: column;
    align-items: flex-end;
  }
}
